.c{
    height: 100vh;
    position: relative; 
}

.c-bg{
    width: 20px;
    height: 100%;
    position: absolute;


    background: linear-gradient(-45deg, #e5b6eb, #f3e1b0, #23a6d5, #23d5ab);
    animation: gradient 5s ease infinite;
    background-size: 400% 400%;
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.c-wrapper{
    padding: 50px;
    display: flex; 
}

.c-left{
    flex: 1;
}

.c-right{
    flex: 1;
    display: flex; 
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-title{
    font-size: 60px;
    width: 80%;
}

.c-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
}

.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px; 
}

.c-desc{
    font-weight: 200;
}


form{
    margin-top: 20px;

}

input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px; 
}

textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px; 
}



@media screen and (max-width: 480px){
    .c-wrapper{
        flex-direction: column;
        padding: 0px 50px; 
    }

    .c-title{
        font-size: 30px;
    }

    .c-info-item{
        margin: 20px 0px;
        width: 100%;
    }

    .c-desc{
        display: none; 
    }

    form{
        margin-top: 0;
        display: flex;
        flex-wrap: wrap;
    }

    input{
        width: 35%;
        height: 40px;
        margin: 10px;
        margin-left: 0;
    }

    button{
        margin-top: 10; 
    }


}